import React, {useState, useEffect} from 'react';
import './App.css';
import Phonebook from './components/phonebook';
import Contacts from './components/newcontact';
import Search from './components/search';
import contactService from './services/person';
import Notice from './components/notifications';
import Error from './components/errornotice';

const App = () => {
const [ persons, setPersons] = useState([]);
const [ newName, setNewName ] = useState('');
const [ newPhone, setNewPhone ] = useState('');
const [ search, setSearch ] = useState('');
const [ message, setMessage] = useState('');
const [ error, setError] = useState('');

useEffect(() => {
  contactService
  .getContact()
  .then(response => {
      setPersons(response.data);
    })
},[])

const personToShow = persons.filter(person => person.name.toLowerCase().includes(search.toLowerCase()));

  const remove = (id) => {
    if(!window.confirm(`Are you sure you want to delete this record`)){
      return;
    } else {
        const person = persons.filter(remaining=>remaining.id !== id);
        contactService
        .removeContact(id)
        .then(response  => {
            setPersons(person);
            setNewName('');
            setNewPhone('');
            setMessage('Record deleted successfully');
            })
            setTimeout(()=>{
              setMessage('')
            }, 3000)
            }
      }


const handleName = (event) => {
  setNewName(event.target.value);
}
const handlePhone = (event) => {
  setNewPhone(event.target.value);
}
const formSubmit = (event) => {
  event.preventDefault();

  const newPerson = {
    name: newName,
    number: newPhone,
  };
  let exists = false;
  persons.forEach(item => {
    if (item.name.toLowerCase() === newName.toLowerCase() && item.number === newPhone) {
      alert (`${newName} and ${newPhone} combination already exists. Please enter a new contact.!`);
      exists = true;
    } else if (!exists && item.name.toLowerCase() === newName.toLowerCase() && item.number !== newPhone){
      if(window.confirm(`${item.name} is already added to the phonebook. Should the number be replaced?`)){
        const contacts = persons.find(changed=>changed.id === item.id);
        const updated = {...contacts, number: newPhone };
        contactService
        .updateContact(updated.id, updated)
        .then(response=> {
            setPersons(persons.map(chngContact=>chngContact.id !== item.id ?chngContact : response.data))
            setNewName('');
            setNewPhone('');
            setMessage('Contact successfully Updated');
          })
          .catch(error => {
            setError(`${updated.name} was already deleted`)
          })
          setTimeout(()=>{
            setMessage('')
          }, 3000)
          setTimeout(()=>{
            setError('')
          }, 3000)
          setPersons(persons.filter(person => person.id !== updated.id));
        }
        exists = true;
      } else {
        return;
      }
  });
  if(!exists){
    contactService
    .addNew(newPerson)
    .then(response  => {
      setPersons(persons.concat(response.data));
      setNewName('');
      setNewPhone('');
      setMessage('Contact successfully Added');
    });
    setTimeout(()=>{
      setMessage('')
    }, 3000)
  };

};

const onSearch = (event) => {
  setSearch(event.target.value);
}

const phonebook = () => {
  if(personToShow.length === 0){
    return (
        <p>There are no contacts!</p>
        )
    }
    return (
      personToShow.map(person=>
        <Phonebook
        key={person.id}
        person={person}
        removeContact={()=>remove(person.id)}
        />
      )
    )
}
return (
 <div>
   <h2>Phonebook</h2>
   <Notice message = {message} />
   <Error message = {error} />
   <Search onSearchChange = {onSearch} searchVal ={search} />
   <h3>Add New Contact</h3>
   <Contacts onChangeName={handleName} onChangePhone = {handlePhone} onSubmit = {formSubmit} nameVal = {newName} phoneVal = {newPhone} />

   <h3>Numbers</h3>
   <ol>{phonebook()}</ol>

 </div>
)
}
export default App;
