import React from 'react';

const Contacts = ({onChangeName, onChangePhone, onSubmit, nameVal, phoneVal}) => {
  return (

  <form onSubmit = {onSubmit} >
    <>
      <div>Name: <input type="text" onChange={onChangeName} value = {nameVal} placeholder="Enter a Name" required/></div>
      <div>Phone: <input type='tel' maxLength='12' onChange={onChangePhone} value = {phoneVal} placeholder="Enter phone no." required /></div>
      <div><button className='addbtn' type="submit">Add</button></div>
    </>
  </form>
)
}
export default Contacts;
