import axios from 'axios'

const baseURL = `https://enigmatic-waters-00270.herokuapp.com/api/persons`;

const getContact = () => {
  return axios.get(baseURL);
}

const addNew = newPerson => {
  return axios.post(baseURL, newPerson);
}

const removeContact = id => {
  return axios.delete(`${baseURL}/${id}`)
}

const updateContact = (id, updated) => {
  return axios.put(`${baseURL}/${id}`, updated)
}

export default {
  getContact,
  addNew,
  removeContact,
  updateContact
}
